"use client"

import type React from "react"

import { useState, useEffect } from "react"
import { Mail, RotateCw } from "lucide-react"

export function TwoFactorForm() {
  const [code, setCode] = useState("")
  const [timeLeft, setTimeLeft] = useState(300) // 5 minutes in seconds
  const [isResending, setIsResending] = useState(false)
  const [isVerifying, setIsVerifying] = useState(false)
  const [message, setMessage] = useState("")
  const [error, setError] = useState("")

  useEffect(() => {
    if (timeLeft <= 0) return

    const timer = setInterval(() => {
      setTimeLeft((prev) => prev - 1)
    }, 1000)

    return () => clearInterval(timer)
  }, [timeLeft])

  const formatTime = (seconds: number) => {
    const minutes = Math.floor(seconds / 60)
    const secs = seconds % 60
    return `${minutes}:${secs.toString().padStart(2, "0")}`
  }

  const handleCodeChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value.replace(/[^0-9]/g, "").slice(0, 6)
    setCode(value)
  }

  const handleVerify = async () => {
    if (code.length !== 6) {
      setError("Veuillez entrer les 6 chiffres")
      return
    }

    setIsVerifying(true)
    setError("")
    setMessage("")

    // Simulate API call
    setTimeout(() => {
      setIsVerifying(false)
      setMessage("Vérification réussie!")
      setCode("")
    }, 1500)
  }

  const handleResend = async () => {
    setIsResending(true)
    setError("")
    setMessage("")

    // Simulate API call
    setTimeout(() => {
      setIsResending(false)
      setTimeLeft(300)
      setMessage("Code renvoyé avec succès à votre email")
      setCode("")
    }, 1500)
  }

  const isCodeExpired = timeLeft <= 0

  return (
    <div className="w-full max-w-md">
      <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-8">
        {/* Header */}
        <div className="text-center mb-8">
          <div className="flex justify-center mb-4">
            <div className="bg-gray-100 p-3 rounded-full">
              <Mail className="w-6 h-6 text-gray-600" />
            </div>
          </div>
          <h1 className="text-2xl font-semibold text-gray-900 mb-2">Vérification en deux étapes</h1>
          <p className="text-gray-600 text-sm">Entrez le code à 6 chiffres reçu par email</p>
        </div>

        {/* Code Input */}
        <div className="mb-6">
          <label className="block text-sm font-medium text-gray-700 mb-3">Code de vérification</label>
          <input
            type="text"
            value={code}
            onChange={handleCodeChange}
            disabled={isCodeExpired}
            placeholder="000000"
            maxLength={6}
            className="w-full px-4 py-3 text-center text-2xl tracking-widest font-mono border border-gray-300 rounded-lg focus:outline-none focus:border-[#1e40af] focus:ring-1 focus:ring-[#1e40af] disabled:bg-gray-50 disabled:cursor-not-allowed"
          />
        </div>

        {/* Timer and Expiration */}
        <div className="mb-6 text-center">
          <p className={`text-sm font-medium ${isCodeExpired ? "text-red-600" : "text-gray-600"}`}>
            Code expire dans: <span className="font-mono">{formatTime(timeLeft)}</span>
          </p>
          {isCodeExpired && (
            <p className="text-sm text-red-600 mt-1">Le code a expiré. Veuillez en demander un nouveau.</p>
          )}
        </div>

        {/* Messages */}
        {error && (
          <div className="mb-4 p-3 bg-red-50 border border-red-200 rounded-lg">
            <p className="text-sm text-red-700">{error}</p>
          </div>
        )}

        {message && (
          <div className="mb-4 p-3 bg-green-50 border border-green-200 rounded-lg">
            <p className="text-sm text-green-700">{message}</p>
          </div>
        )}

        {/* Verify Button */}
        <button
          onClick={handleVerify}
          disabled={isVerifying || code.length !== 6 || isCodeExpired}
          className="w-full bg-[#1e40af] hover:bg-[#1e3a8a] disabled:bg-gray-300 disabled:cursor-not-allowed text-white font-medium py-3 rounded-lg transition-colors mb-4"
        >
          {isVerifying ? "Vérification..." : "Vérifier le code"}
        </button>

        {/* Resend Button */}
        <button
          onClick={handleResend}
          disabled={isResending}
          className="w-full flex items-center justify-center gap-2 border border-gray-300 hover:border-gray-400 hover:bg-gray-50 text-gray-700 font-medium py-3 rounded-lg transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
        >
          <RotateCw className="w-4 h-4" />
          {isResending ? "Envoi..." : "Renvoyer le code"}
        </button>

        {/* Help Text */}
        <p className="text-center text-xs text-gray-500 mt-6">
          Vous n'avez pas reçu le code? Vérifiez votre dossier spam.
        </p>
      </div>
    </div>
  )
}
